<template>
  <div class="seePage">
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item>库存管理</el-breadcrumb-item>
      <el-breadcrumb-item class="attrbute">
        <span @click="goBack">胎号管理</span>
      </el-breadcrumb-item>
      <el-breadcrumb-item>查看胎号</el-breadcrumb-item>
    </el-breadcrumb>
    <div class="search_container" v-clock>
      <el-form :inline="true" :model="infoParams" ref="TableDataRef">
        <el-form-item label="胎号" label-width="110px" size="mini">
          <el-input v-model="infoParams.tire_un"></el-input>
        </el-form-item>
        <el-form-item label="编号(条形码)" label-width="110px" size="mini">
          <el-input v-model="infoParams.tire_sn"></el-input>
        </el-form-item>
        <el-form-item size="mini" class="search-style search">
          <el-button @click="onSearch" size="mini" type="primary">查询</el-button>
          <el-button plain @click="onReset" size="mini">重置</el-button>
        </el-form-item>
      </el-form>
    </div>
    <div class="add_delet">
      <el-button plain icon="el-icon-delete" @click="deletes" size="mini">删行</el-button>
    </div>
    <div class="table_container">
      <el-table :highlight-current-row="true" tooltip-effect="light" border :data="TableData" style="width: 100%" @selection-change="handleSelectionChange" max-height="600">
        <el-table-column :show-overflow-tooltip="true" type="selection" fixed="left" align="center" label="选择" width="44"></el-table-column>
        <el-table-column label="序号" type="index" width="46" align="center"></el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="tire_un" label="胎号">
          <template slot-scope="scope">
            <span v-if="!scope.row.newFlag">{{scope.row.tire_un}}</span>
            <el-input v-model="scope.row.tire_un" placeholder="请输入" v-else></el-input>
          </template>
        </el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="tire_sn" label="编号(条形码)">
          <template slot-scope="scope">
            <span v-if="!scope.row.newFlag">{{scope.row.tire_sn}}</span>
            <el-input v-model="scope.row.tire_sn" placeholder="请输入" v-else></el-input>
          </template>
        </el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="remark" label="备注">
          <template slot-scope="scope">
            <span v-if="!scope.row.newFlag">{{scope.row.remark}}</span>
            <el-input v-model="scope.row.remark" maxlength="300" placeholder="请输入" v-else></el-input>
          </template>
        </el-table-column>
      </el-table>
      <div class="page_container">
        <div class="pageNumber">每页显示</div>
        <el-pagination class="erp-pagination" @size-change="handleSizeChange" :current-page.sync="goPage" :page-size="pageSize1" @current-change="handleCurrentChange" :page-sizes="per_page" layout="total,sizes, prev, pager, next,jumper" prevText="上一页" nextText="下一页" :total="total">
        </el-pagination>
      </div>
    </div>
    <div slot="footer" class="dialog-footer">
      <el-button @click="goBack">返 回</el-button>
      <!-- <el-button type="primary" @click="submit">确 定</el-button> -->
    </div>
  </div>
</template>

<script>
import { mapActions, mapGetters } from "vuex";
import { getData } from "common/utils/public_function/commonUtils";
export default {
  name: "instorageTiremanagementTiremanagementSee",
  data() {
    return {
      authority: {},
      TableData: [],
      infoParams: {}, //
      goPage: 1, //默认搜索页面
      pageSize: 30, //默认表格size
      loading: true,
      total: 10,
      per_page: [30, 50, 100, 500],
      multipleSelection: []
    };
  },
  computed: {
    ...mapGetters(["tiremanagementIndexGet", "tiremanagementSeeGet"])
  },
  methods: {
    ...mapActions(["tiremanagementIndexACT"]),
    handleSelectionChange(val) {
      this.multipleSelection = val;
    },
    goBack() {
      this.$router.push({
        path: "/inventory/tiremanagement"
      });
    },
    initData() {
      this.$axMethod.tireGetTireList(this.infoParams).then(({ data }) => {
        if (data.code == "200") {
          this.TableData = data.data.data;
          this.total = data.data.total;
        }
      });
    },
    handleSizeChange(val) {
      Object.assign(this.infoParams, {
        per_page: val,
        page: 1
      });
      this.initData();
    },
    handleCurrentChange(val) {
      Object.assign(this.infoParams, {
        page: val
      });
      this.initData();
    },
    onReset() {
      /** 重置时 如需保存默认参数，则： */
      this.infoParams = {
        tire_goods_id: this.tiremanagementSeeGet.row.tire_goods_id
      };
      this.initData();
    },
    onSearch() {
      Object.assign(this.infoParams, {
        page: 1
      });
      this.initData();
    },
    deletes() {
      if (this.multipleSelection.length) {
        this.$confirm("确定要删除此条信息？", "提示", {
          confirmButtonText: "确定",
          cancelButtonText: "取消",
          type: "warning"
        })
          .then(() => {
            let addr = [];
            let tireArry = this.multipleSelection.map(val => {
              return val.tire_id;
            });
            this.$axMethod
              .delTire({
                tire_id: tireArry.toString(),
                tire_goods_id: this.tiremanagementSeeGet.row.tire_goods_id
              })
              .then(res => {
                if (res.data.code == 200) {
                  this.$message({
                    message: res.data.msg,
                    type: "success"
                  });
                  this.initData();
                }
              });
          })
          .catch(() => {
            this.$message({
              type: "info",
              message: "已取消删除"
            });
          });
      } else {
        this.$message({
          message: "请先选择要删除的商品！",
          type: "warning"
        });
      }
    }
  },
  created() {
    this.infoParams = {
      tire_goods_id: this.tiremanagementSeeGet.row.tire_goods_id
    };
    this.initData();
  }
};
</script>
